<template>
	<div >
           <div class="comment-head">
                <span v-if="item.sendAvatar === null"   class="notice-icon" >  
                    <span    class="notice-icon-font" ></span>  
                </span>
                <img v-if="item.sendAvatar" :src="item.sendAvatar">
                </div><!---->
                <div class="comment-content">
                <div class="commentuser-margin-bottom">
                    <span v-if="item.sendAvatar === null" class="font-span commentuser-margin-right"></span>
                    <span v-else class="font-span commentuser-margin-right">{{item.sendNickname}}</span>
                    <em class="font-em">{{item.sendDate}}</em>
                    </div>
                <!---->
                    <div>
                        <!--1 2 3 4 5 6-->
                        <p  v-if="item.action === 1" class="font-span margin-bottom-16">{{item.targetName}}</p>
                        <p  v-if="item.action === 2" class="font-span margin-bottom-16">{{item.targetName}}</p>
                        <!-- <p  v-if="item.action === 3" class="font-span margin-bottom-16">{{item.sendCommentContent}}</p> -->
                        <!-- <p  v-if="item.action === 4" class="font-span margin-bottom-16">{{item.sendCommentContent}}</p> -->
                        <p  v-if="item.action === 5" class="font-span margin-bottom-16"></p>
                        <p  v-if="item.action === 6" class="font-span margin-bottom-16">{{item.targetId}}</p>
                    </div><!---->

                </div><!---->
	</div>    
      

</template>

<script>
import '../../../assets/picture/workDetailcss.css'

export default {
    name: 'noticeMail',
    props:['item','index'],


    data () {
	  return {
            /** XX (placeholder)*/
            answerCommentUser:'',
            /**/
            commentIndex:-1,
            /**/
            answerCommentValue:'',
            page:1,
			
	  }
    },
    methods:{
        CommentCurrentPage(page){
            this.$emit('CommentCurrentPage',[{pageNum:page},{type:2}])
        }
    }
}
	
</script>

<style scoped>
   


/**/
/**/
    .deleteBtn{
        text-align: right;
        padding-top: 5px;
    }
    /**/
    .deleteStyle{
        color: #515151;
        cursor: pointer;
    }
    .deleteStyle:hover{
        color:#aba2a2
    }
</style>


