<template>
	<div >
        <div class="comment-head"><img :src="item.sendAvatar"></div><!---->
        <div class="comment-content">
            <div class="commentuser-margin-bottom">
                <span class="font-span commentuser-margin-right-17">{{item.sendNickname}}</span>
                <!--action 3  4-->
                <!--targetType = 1   2-->
                <span v-if="item.action === 3" class="font-span commentuser-margin-right">{{item.targetName}}</span>
                <span v-if="item.action === 4" class="font-span commentuser-margin-right">{{item.targetName}}</span>
                <em  class="font-em">{{item.sendDate}}</em>
                    <span class="font-span answer-style" @click="showAnswerStyle(item,index)"></span>
            </div><!---->
            
            <div><p class="font-span margin-bottom-16 ">{{item.sendCommentContent}}</p></div><!---->
            <answer-component :item="item" :index="index" :commentIndex="commentIndex" @changeCommentIndex = 'changeCommentIndex'  @addAnswerFun = 'addAnswerFun' @deleteComment= 'deleteComment'></answer-component>
        </div><!---->
	</div>
</template>

<script>
import '../../../assets/picture/workDetailcss.css'
import answerComponent from './answer'
import { constants, copyFile } from 'fs';
export default {
    name: 'noticeMail',
    props:['item','index'],
    components:{
       'answer-component': answerComponent
    },
    data () {
	  return {
            /** XX (placeholder)*/
            answerCommentUser:'',
            /**/
            commentIndex:-1,
            
            /**/
            answerCommentValue:'',
	  }
    },
  
    methods:{
         /* */
        showAnswerStyle(item,index){
            this.commentIndex = this.commentIndex === index ? -1 : index;
            if(this.commentIndex === index){
                 this.answerCommentUser = ''+ item.sendNickname;
            }
        },
        //
        changeCommentIndex(){
            this.commentIndex = -1;
        },
        
       addAnswerFun(value){
           this.$emit('addComment',value);
        //    this.$emit('addComment2',value);
       },
        CommentCurrentPage(page){
            //  this.page = page;
            this.$emit('CommentCurrentPage',[{pageNum:page},{type:2}])
        },
        //
        deleteComment(value){
            this.$emit('deleteCommentFun',value);
        }
    }
}
	
</script>

<style scoped>
   


/**/
/**/
    .deleteBtn{
        text-align: right;
        padding-top: 5px;
    }
    /**/
    .deleteStyle{
        color: #515151;
        cursor: pointer;
    }
    .deleteStyle:hover{
        color:#aba2a2
    }
</style>


